@use "../abstracts/colors" as *;

.hero {
    margin-top: 1rem;
    width: 100%;
    z-index: 0;
}

.hero__img-container {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: -1;

    img {
        width: 100%;
        height: 100%;
    }
}

.hero__text {
    padding: 25px;
    margin-top: 1rem;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 1rem;
}

.hero__title {
    font-size: 1.89rem;
    font-weight: 700;
    color: $almost-black;
}

.hero__subtitle {
    line-height: 1.2rem;
    font-size: 0.85rem;
    font-weight: 500;
    color: $medium-gray;
    text-align: center;
}

.hero__btn {
    background-color: $almost-black;
    color: $almost-white;
    font-size: 0.8rem;
    font-weight: 700;
    padding: 1em 1.5em;
    border-radius: 15px;
    cursor: pointer;
}

.hero__sponsor-container {
    padding: 20px;
    max-width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;

    img {
        width: 100%;
        flex: 1 1 25%;
    }
}
